import React, { Component } from 'react'
import Helmet from 'react-helmet'
import Layout from 'components/Layout'
import SEO from 'components/SEO'
import Title from 'components/Title'

// import { Container } from './styles';

export default class stacks extends Component {
  render() {
    return (
      <Layout>
        <SEO
          title="Stacks"
          description="Projects developed for study"
          keywords="React, React JS, React Native, Node, Front-end, Back-end, API, developer"
        />
        <Helmet>
          <script
            async
            src="https://cdn1.stackshare.io/javascripts/client-code.js"
            charset="utf-8"
          ></script>
        </Helmet>
        <section className="section content is-size-4-desktop is-size-5-touch">
          <Title>Stacks</Title>
          <p className="has-text-centered">
            Follow me on <a href="https://github.com/leandrodsi">GitHub</a>.
          </p>
          <div className="columns is-centered">
            <div className="column is-half">
              <a
                frameborder="0"
                data-theme="dark"
                data-layers="1,2,3,4"
                data-stack-embed="true"
                href="https://embed.stackshare.io/stacks/embed/a3bd36576fa71d78148bf42bf713ad"
              >
                Stackshare
              </a>
            </div>
          </div>
        </section>
      </Layout>
    )
  }
}
